<template>
  <div class="userList">
    <bread></bread>
    <table>
      <tr>
        <th style="width: 20em">头像/名称 <span class="people_info">(点击查看详情)</span></th>
        <th>性别</th>
        <th>城市</th>
        <th>省份</th>
        <th>国家</th>
        <th>注册时间</th>
        <th>操作</th>
      </tr>
      <tr class="td" v-for="(item,key) in data">
        <td>
          <a href="javascript:void(0)" class="nickname" @click="seeInfo(item)">
            <img :src="item.avatarUrl | img" width="40px"  style="border-radius: 50%;margin-right: 5px;"/>
            <span style="display: inline-block;width: 150px;text-align: left;color: #2277da">
              {{item.nickName || "无昵称"}}
            </span>
          </a>
        </td>
        <td>{{item.gender}}</td>
        <td>{{item.city}}</td>
        <td>{{item.province }}</td>
        <td>{{item.country}}</td>
        <td>{{item.create_time}}</td>
        <td>
          <template>
            <i-switch size="large" v-model="item.is_forbidden" @on-change="change(item,key)" true-value="0" false-value="1">
              <span slot="open">启用</span>
              <span slot="close">禁用</span>
            </i-switch>
          </template>
        </td>
      </tr>
    </table>
    <p v-if="data.length===0" class="td_data">
      {{msg}}
    </p>
    <div class="ter_page">
      <Page :total="num" :page-size-opts="[10,20,30,40]" :page-size="currentPage" size="small" show-total show-sizer @on-change="pageNnumber" @on-page-size-change="pageRow">
      </Page>
    </div>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  import page from '../page.vue'
  export default {
    data() {
      return {
        msg: "正在加载....",
        data: [],
        value: '',
        currentPage: 10,
        //      每页条数
        current: 1,
        //      总条数
        num: 0,
      }
    },

    methods: {
      remove(index) {
        this.data1.splice(index, 1);
      },
      change(index) {
        let user = "user"
        this.$Modal.confirm({
          title: '是否修改',
          content: '<p>确定要启用或关闭该用户吗</p>',
          onOk: () => {
            this.post("system/Info/forbiddenById", {
              id: index.id,
              type: user,
              is_forbidden: index.is_forbidden
            }).then(res => {
              if(res.data.code === 10000) {
                const content = res.data.msg
                this.$Modal.success({
                  title: "是否修改",
                  content: content
                });
              } else {
                const content = res.data.msg
                this.$Modal.error({
                  title: "删除失败",
                  content: content
                });
              }
            });

          },

        });
      },
      seeInfo(index) {
        let url = "terDetail/" + index.id
        this.$router.push({
          path: url
        })
      },
      getUserList(current, currentPage) {
        this.post("system/Info/getUserList", {
          page: this.current,
          limit_num: this.currentPage,
        }).then(res => {
          if(res.data.code === 10000) {
            console.log(res.data)
            this.data = res.data.data.info;
            this.num = res.data.data.number;
            this.data.forEach(item => {
              let time = new Date(item.create_time * 1000);
              item.is_forbidden = `${item.is_forbidden}`;
              item.create_time = time.getFullYear()+'/'+ (time.getMonth()-0+1)+'/'+time.getDate() + ' ' + time.getHours()+':'+time.getMinutes();

            })
            this.data.forEach(item => {
              if(item.gender === "0") {
                item.gender = "男"
              } else {
                item.gender = "女"
              }

            })

          } else {
            this.data = "暂无数据"
            const content = res.data.msg_test
            this.$Modal.error({
              title: "获取失败",
              content: content
            });
          }
        });
      },
      pageNnumber(val) {
        this.current = val
        this.getUserList(this.current,this.currentPage);
      },
      pageRow(val) {
        this.currentPage = val
        this.getUserList(this.current,this.currentPage);
      },
    },
    created() {
      this.getUserList()
    },
    components: {
      bread,
      page
    },
    filters:{
      img (val) {
          return val ? val : `${require('../../assets/noImg.png')}`
      }
    }
  }
</script>
<style scoped>
  .userList {
    width: 98%;
    margin: 0 auto;
  }

  table {
    width: 100%;
    font-size: 14px
  }

  th {
    padding: 10px 20px;
    background: #F5F5F5;
    text-align: center;
    border: 1px solid #E9ECEF;
  }

  .td>td {
    padding: 6px 10px;
    border-bottom: 1px solid #f2f2f2;
    text-align: center;
  }

  .info {
    color: white;
    background: #93B719;
    width: 85px;
    height: 30px;
    line-height: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 4px;
  }

  td {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border: 1px solid #E9ECEF;
    background: white;
  }

  .td_data {
    width: 100%;
    padding: 20px 10px;
    text-align: center;
    border-bottom: 1px solid #f2f2f2;
  }

  a:hover {
    opacity: 0.6;
    text-decoration: none
  }

  .ter_page {
    text-align: center;
    margin-top: 40px;
  }
  .people_info{
    font-size: 1em;
    font-weight: normal;
    color: #444;
    letter-spacing: 0.2em;
    margin-left: 5px;
  }
  .nickname{
    color: black;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .nickname:hover{
    color: #6098FF;
  }
</style>



